.ads {
    background: $ads;
    display: flex;
    justify-content: center;
    align-items: center;
    color: $white;
    padding: 20px 0;

    &__icon {
        line-height: 40px;
        font-size: 55px;
        margin-right: 24px;
    }
    &__content {
        font-size: 16px;
        max-width: 166px;
    }
}