@font-face {
  font-family: "Montserrat";
  src: url(../fonts/Montserrat/Montserrat-Regular.ttf);
  font-weight: 400;
}

@font-face {
  font-family: "Montserrat";
  src: url(../fonts/Montserrat/Montserrat-Bold.ttf);
  font-weight: 700;
}

@font-face {
  font-family: "Comfortaa";
  src: url(../fonts/Comfortaa/Comfortaa-Bold.ttf);
  font-weight: 700;
}

@font-face {
  font-family: "Comfortaa";
  src: url(../fonts/Comfortaa/Comfortaa-Regular.ttf);
  font-weight: 400;
}

@font-face {
  font-family: "fontawesome";
  src: url(../fonts/fontawesome/fontawesome-duotone.ttf);
  font-weight: 900;
}

@font-face {
  font-family: "fontawesome";
  src: url(../fonts/fontawesome/fontawesome-light.ttf);
  font-weight: 300;
}

@font-face {
  font-family: "Roboto";
  src: url(../fonts/Roboto/Roboto-Medium.ttf);
  font-weight: 500;
}

@font-face {
  font-family: "Roboto";
  src: url(../fonts/Roboto/Roboto-Regular.ttf);
  font-weight: 400;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

body {
  font-family: "Comfortaa";
  background: #040D1F;
}

.container {
  max-width: 1140px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media (max-width: 1140px) {
  .container {
    max-width: 992px;
  }
}

@media (max-width: 992px) {
  .container {
    max-width: 768px;
  }
}

@media (max-width: 768px) {
  .container {
    max-width: 576px;
  }
}

@media (max-width: 576px) {
  .container {
    max-width: 100%;
  }
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

::after, ::before {
  font-family: "fontawesome";
}

.icon-film::before {
  content: '\f008';
  font-weight: 300;
  color: #fff;
}

.icon-play::before {
  content: '\f04b';
  font-weight: 900;
  color: #fff;
  position: absolute;
}

.icon-play::after {
  content: '\10f04b';
  font-weight: 900;
  color: #fff;
  opacity: 0.4;
}

.icon-burger::before {
  content: '\f0c9';
  font-weight: 300;
}

.btn {
  font-family: "Roboto";
  font-weight: 500;
  font-size: 14px;
  line-height: 12px;
  color: #000;
  background: #F18128;
  padding: 17px 48px;
  border-radius: 4px;
  margin-right: 30px;
  /* display: block;
    width: max-content; */
  display: inline-block;
  transition: .5s;
}

.btn:hover {
  background: #040D1F;
  color: #fff;
}

.title {
  font-size: 36px;
  font-weight: 400;
  margin-top: 60px;
  margin-bottom: 48px;
  display: flex;
}

.title__link {
  color: #F18128;
  text-transform: capitalize;
}

.title__link::after {
  content: '';
  display: block;
  height: 2px;
  background: #F18128;
  margin-top: 2px;
}

.ads {
  background: linear-gradient(90.09deg, #3800AF 0%, #BF7204 97.52%, #C27500 100%, #C27500 100%);
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  padding: 20px 0;
}

.ads__icon {
  line-height: 40px;
  font-size: 55px;
  margin-right: 24px;
}

.ads__content {
  font-size: 16px;
  max-width: 166px;
}

.header {
  background: rgba(1, 3, 5, 0.8);
  backdrop-filter: blur(15px);
  position: sticky;
  top: 0;
  z-index: 999;
}

.nav {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.nav a {
  color: #fff;
}

.nav__burger {
  background: none;
  border: none;
  color: #fff;
  font-size: 35px;
  outline: none;
  line-height: 40px;
  padding: 30px 5px;
  visibility: hidden;
}

@media (max-width: 768px) {
  .nav__burger {
    visibility: visible;
  }
}

.logo {
  font-weight: 700;
  font-size: 18px;
}

.logo img {
  margin-right: 5px;
}

.menu {
  display: flex;
  flex-wrap: wrap;
}

@media (max-width: 768px) {
  .menu {
    position: absolute;
    right: 20px;
    top: 100px;
    background: rgba(1, 3, 5, 0.8);
    flex-direction: column;
    min-width: 200px;
    transform: scaleY(0);
    transition: .5s;
    transform-origin: 50% 0 0;
    opacity: 0;
  }
  .menu.active {
    transform: scaleY(1);
    opacity: 1;
  }
}

.menu__link {
  font-family: "Montserrat";
  font-size: 14px;
  padding: 43px 6px 40px 6px;
  display: block;
  margin-left: 18px;
  transition: .5s;
  position: relative;
}

@media (max-width: 768px) {
  .menu__link {
    padding: 20px;
    margin-left: 0;
  }
}

.menu__link::after {
  content: '';
  width: 0;
  height: 2px;
  background: #ed8e00;
  display: block;
  position: absolute;
  bottom: 0;
  left: 50%;
  border-radius: 2px 2px 0 0;
  transition: .5s;
}

@media (max-width: 768px) {
  .menu__link::after {
    height: 0;
    width: 2px;
    left: 0;
    bottom: 50%;
  }
}

.menu__link:hover {
  color: #ed8e00;
}

@media (max-width: 768px) {
  .menu__link:hover {
    background: rgba(237, 142, 0, 0.1);
  }
}

.menu__link:hover::after {
  width: 100%;
  left: 0;
}

@media (max-width: 768px) {
  .menu__link:hover::after {
    height: 100%;
    width: 2px;
    bottom: 0;
  }
}

.banner {
  min-height: 100vh;
  position: relative;
  display: flex;
  flex-direction: column;
}

.banner__img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.banner__content {
  position: relative;
  background: rgba(7, 15, 25, 0.75);
  height: 100%;
  flex-grow: 1;
  display: flex;
  align-items: center;
  font-family: "Montserrat";
  padding: 15px 0;
  color: #fff;
}

.banner__title {
  font-size: 64px;
  font-weight: 700;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

@media (max-width: 768px) {
  .banner__title {
    font-size: 50px;
  }
}

@media (max-width: 576px) {
  .banner__title {
    font-size: 30px;
    text-align: center;
  }
}

.banner__desc {
  font-size: 16px;
  line-height: 24px;
  text-align: justify;
  color: #C8C8C8;
  max-width: 570px;
  margin-top: 22px;
  margin-bottom: 48px;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

@media (max-width: 576px) {
  .banner__desc {
    text-align: center;
  }
}

.banner__links {
  display: flex;
}

@media (max-width: 576px) {
  .banner__links {
    flex-direction: column;
    align-items: center;
  }
  .banner__links .btn {
    margin-bottom: 20px;
    margin-right: 0px;
  }
}

.film {
  position: relative;
  border-radius: 6px;
  overflow: hidden;
}

@media (max-width: 992px) {
  .film {
    min-height: 380px;
  }
}

@media (max-width: 576px) {
  .film {
    min-height: auto;
  }
}

.film__img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.film__content {
  height: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #fff;
  font-family: "Montserrat";
  background: linear-gradient(180deg, rgba(241, 129, 40, 0.35) 0%, rgba(0, 0, 0, 0.71) 51.04%, rgba(7, 15, 25, 0.8) 100%);
  padding: 56px 15px 19px 15px;
}

.film__link {
  line-height: 48px;
  font-size: 48px;
  margin-bottom: 13px;
}

.film__time {
  font-size: 14px;
  background: rgba(7, 15, 25, 0.2);
  line-height: 10px;
  padding: 7px 28px;
  border-radius: 9px;
  border: 1px solid #fff;
  margin-bottom: 32px;
}

.film__title {
  font-weight: 400;
  font-size: 22px;
  margin-bottom: 13px;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.film__desc {
  font-family: "Roboto";
  font-size: 14px;
  line-height: 21px;
  display: -webkit-box;
  -webkit-line-clamp: 7;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.films {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 30px;
}

@media (max-width: 992px) {
  .films {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 576px) {
  .films {
    grid-template-columns: 100%;
  }
}
