.film {
    position: relative;
    border-radius: 6px;
    overflow: hidden;
    @include media($md) {
        min-height: 380px;
        // display: flex;
        // flex-direction: column;
    }

    @include media($xs) {
        min-height: auto;
    }

    &__img {
        position: absolute;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__content {
        // flex-grow: 1;
        height: 100%;
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        color: $white;
        font-family: "Montserrat";
        background: $film;
        padding: 56px 15px 19px 15px;
    }

    &__link {
        line-height: 48px;
        font-size: 48px;
        margin-bottom: 13px;
    }

    &__time {
        font-size: 14px;
        background: $time;
        line-height: 10px;
        padding: 7px 28px;
        border-radius: 9px;
        border: 1px solid $white;
        margin-bottom: 32px;
    }

    &__title {
        font-weight: 400;
        font-size: 22px;
        margin-bottom: 13px;
        @include limit(1);
    }

    &__desc {
        font-family: "Roboto";
        font-size: 14px;
        line-height: 21px;
        @include limit(7)
    }
}

.films {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 30px;
    @include media($md) {
        grid-template-columns: repeat(2, 1fr);
    }
    @include media($xs) {
        grid-template-columns: 100%;
    }
}