.header {
    background: $nav;
    backdrop-filter: blur(15px);
    position: sticky;
    top: 0;
    z-index: 999;
}

.nav {
    @include row (0);
    justify-content: space-between;
    align-items: center;

    a {
        color: $white;
    }

    &__burger {
        background: none;
        border: none;
        color: $white;
        font-size: 35px;
        outline: none;
        line-height: 40px;
        padding: 30px 5px;
        visibility: hidden;
        @include media($sm) {
            visibility: visible;
        }
    }
}

.logo {
    font-weight: 700;
    font-size: 18px;

    img {
        margin-right: 5px;
    }
}

.menu {
    @include row (0);
    @include media($sm) {
        position: absolute;
        right: 20px;
        top: 100px;
        background: $nav;
        flex-direction: column;
        min-width: 200px;
        transform: scaleY(0);
        transition: .5s;
        transform-origin: 50% 0 0;
        opacity: 0;
        &.active {
            transform: scaleY(1);
            opacity: 1;
        }
    }
    &__link {
        font-family: "Montserrat";
        font-size: 14px;
        padding: 43px 6px 40px 6px;
        display: block;
        margin-left: 18px;
        transition: .5s;
        position: relative;
        @include media($sm) {
            padding: 20px;
            margin-left: 0;
        }

        &::after {
            content: '';
            width: 0;
            height: 2px;
            background: $nav__link;
            display: block;
            position: absolute;
            bottom: 0;
            left: 50%;
            border-radius: 2px 2px 0 0;
            transition: .5s;
            @include media($sm) {
                height: 0;
                width: 2px;
                left: 0;
                bottom: 50%;
            }
        }

        &:hover {
            color: $nav__link;
            @include media($sm) {
                background: rgba($nav__link, 0.1);
            }
            &::after {
                width: 100%;
                left: 0;
                @include media($sm) {
                    height: 100%;
                    width: 2px;
                    bottom: 0;
                }
            }
        }
    }
}