.banner {
    min-height: 100vh;
    position: relative;
    display: flex;
    flex-direction: column;

    &__img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__content {
        position: relative;
        background: $banner;
        height: 100%;
        flex-grow: 1;
        display: flex;
        align-items: center;
        font-family: "Montserrat";
        padding: 15px 0;
        color: $white;
    }

    &__title {
        font-size: 64px;
        font-weight: 700;
        @include limit(1);

        @include media($sm) {
            font-size: 50px;
        }
        @include media($xs) {
            font-size: 30px;
            text-align: center;
        }
    }

    &__desc {
        font-size: 16px;
        line-height: 24px;
        text-align: justify;
        color: $grey;
        max-width: 570px;
        margin-top: 22px;
        margin-bottom: 48px;

        @include limit(4);
        @include media($xs) {
            text-align: center;
        }
    }

    &__links {
        display: flex;
        @include media($xs) {
            flex-direction: column;
            align-items: center;

            .btn {
                margin-bottom: 20px;
                margin-right: 0px;
            }
        }
    }
}