* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

a {
    text-decoration: none;
}

ul {
    list-style: none;
}

body {
    font-family: "Comfortaa";
    background: $main;
}

.container {
    @include container();
}

.row {
    @include row();
}

::after, ::before {
    font-family: "fontawesome";
}

.icon {
    &-film::before {
        content: '\f008';
        font-weight: 300;
        color: $white;
    }
    &-play::before {
        content: '\f04b';
        font-weight: 900;
        color: $white;
        position: absolute;
    }
    &-play::after {
        content: '\10f04b';
        font-weight: 900;
        color: $white;
        opacity: 0.4;
    }
    &-burger::before {
        content: '\f0c9';
        font-weight: 300;
    }
}

.btn {
    font-family: "Roboto";
    font-weight: 500;
    font-size: 14px;
    line-height: 12px;
    color: $black;
    background: $orange;
    padding: 17px 48px;
    border-radius: 4px;
    margin-right: 30px;
    /* display: block;
    width: max-content; */
    display: inline-block;
    transition: .5s;

    &:hover {
        background: $main;
        color: $white;
    }
}

.title {
    font-size: 36px;
    font-weight: 400;
    margin-top: 60px;
    margin-bottom: 48px;
    display: flex;

    &__link {
        color: $orange;
        text-transform: capitalize;

        &::after {
            content: '';
            display: block;
            height: 2px;
            background: $orange;
            margin-top: 2px;
        }
    }
}

